import React, { Component } from 'react';
import partnerImg from './img/partnerImg.jpg'
import joinUsImg from './img/people.jpg'
import ArticleTeaser from './ArticleTeaser';
import {BrowserRouter as Router, Route, withRouter} from 'react-router-dom'
import {APIarticles }from './ArticlesAPI'

class Articles extends Component {
        state = { 
            articlesList: []
         }
         componentWillMount() {
            this.setState({
              articlesList: APIarticles ,
            });
          }
    render() { 
        const articlesTeasers = this.state.articlesList.map(item => <ArticleTeaser 
                key = {item.id}
                // onclick = {this.handleOnClick}
                img = {item.img}
                title = {item.title}
                body = {item.body.substring(0,300) + ' ...'}
                btn = {'Czytaj'}
                href = {item.href}
                {...this.props}
                />)
        return (
           

            <div className="articlesSection">
                {articlesTeasers}
            </div>
          
         );
    }
}
 
export default withRouter(Articles);