class CategoryCurrencyArticleTeaser extends Component {
  render() {
    return(
    <div className="articleTeaserBox" onClick={this.onClick}>
        <div className="articleTeaserBox__img" style = {{backgroundImage: `url(${this.props.img} )` }} ></div>
        <h1 className="articleTeaserBox__title">{this.props.title}</h1>
        <div className="articleTeaserBox__line"></div>
        <button className="articleTeaserBox__btn">Czytaj</button>   
    </div>
    )
  }

  onClick = () => {
    this.props.history.push(this.props.href)
  }
}


class CategoryCurrency extends Component {
  constructor() {
    super();
    this.state = {

  articlesList: [],
}
  }
  componentWillMount() {
    this.setState({
      articlesList: APIarticles ,
    });
  }
  render() {
    
    
    
    const panelsFirstRow = this.state.articlesList.map(item => (
      <CategoryCurrencyArticleTeaser
        key={item.id}
        id={item.id}
        img={item.img}
        title={item.title}
        href={item.href}
        {...this.props}
      />
    ))
    
    // SliderFunction()
    return (
     <div>
       
      <section className="articlesTeaserSection">
        <div className="articlesTeaserSection__row articlesTeaserSection__firstRow">
        
          {panelsFirstRow.filter(item => item.props.id <= 4)}
          
        </div>
        <div className="articlesTeaserSection__row articlesTeaserSection__secondRow">
    
          {panelsFirstRow.filter(item => item.props.id > 4 && item.props.id <= 8)}
    
        </div>
        
      </section>
    </div>
    )
  }


 
}

export default withRouter(CategoryCurrency);